<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .loginDiv{
    position: fixed;
    height: 100%;
    width: 100%;
    top: -100%;
    left: 0;
    z-index: 1000;
    background: linear-gradient(180deg,#212335,#4e505f,#212335);
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }
  .loginDiv.active{
    top: 0;
  }

  .login-box{
    position: absolute;
    width: 100%;
    top: calc(50% - 300px);
  }

  .login-footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    padding: 5px 10px;
    color: #999;
    font-size: 10px;
  }
</style>
