<template>
  <div class="container-fluid">
    <div class="row pb-3">
      <div class="col-12 mt-3">
        <h2>Feedback</h2>
      </div>
      <div class="col-12 mt-3 text-right">
        <div class="box">
            <h4 class="float-left">What's your feedback?</h4>
            <textarea name=""  class="form-control" id="feddbackTextarea" rows="7"></textarea>
            <button class="btn btn-info mt-2" @click="feedbackSubmit()">Submit</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"; //載入 axios
import '../../assets/lib/nativescript-webview-interface.js'
var oWebViewInterface = window.nsWebViewInterface;


export default {
  props: {
    
  },
  mounted:function(){
    
  },
  data(){
    return {
      
    }
  },
  methods:{
    feedbackSubmit(){
      this.$store.state.loading_status = true;
      var userName = this.$store.state.GBLoginInfo.user_name;
      var value = document.getElementById('feddbackTextarea').value;
      var time = new Date();
      
      axios.get('https://script.google.com/macros/s/AKfycbxKUBI0dlqPRGbGxHQu5rgsFY1JPhUg4qf5bJ7V6doZskr47zR2/exec?name='+userName+
        '&value='+value+
        '&time='+time
      )
      .then(response=>{
        console.log(response);
        if(response.data == "success"){
          // alert("成功");
          // Thank you for your feedback.
          oWebViewInterface.emit('alertMessage', {title:"Thank You!" , message:"Thank you for your feedback." }); //傳送更新訊息
          this.$store.state.loading_status = false;
          document.getElementById('feddbackTextarea').value = '';
        }
      })
      .catch(function (error) {
         console.log(error);
         
      });
    },

  }
}
</script>

